import React, { useEffect , useState } from 'react';
import GlobeAnimation from '../component/3d';
import profilImg from "../Assets/profil.png"
import linkedin from "../Assets/linkedin.png"
import gitHub from '../Assets/github.png'
import biggithub from '../Assets/icons8-github-100.png'
import bdd from '../Assets/data base.png'
import coding from '../Assets/coding.png'
import chevronUp from '../Assets/icons8-chevron-haut-48.png'
import chevronDwon from '../Assets/icons8-chevron-bas-48.png'
import cv from '../Assets/diagne-alioune-cv.pdf'

const Accueil = () => {
  const [showExperience, setShowExperience] = useState(false);
  const [showProjet, setShowProjet] = useState(false);

  

  const toggleExperience = () => {
    setShowExperience(!showExperience);
  };

  const toggleProjet = () => {
    setShowProjet(!showProjet);
  };
  
  return (
      <div className='Accueil'>
        <div className='first-page'>
          <div className='presentation'>
            <div className='left'>
              <div>
              
            <h1>Bonjour je suis <span >Alioune</span> </h1>
            <h2>Je suis tudiant en <span> BTS SIO </span>  </h2>
            <h3>option SLAM</h3>
            
            <div className='bottom_me'>
            <a href={cv} target="_blank"><button>consultez mon cv</button></a>
            </div>
            </div>
            </div>
            <div className='Canvas'>
          <GlobeAnimation/>
          </div>
          </div>
          <div className='Profil'>
            <div className='box'></div>
            <div className='in-Profil'>
              <div className='img-profil'>
              <img src={profilImg} alt="" />
              </div>
              <div className='text-presentation'>
                <div>
                  <h1>A propos de moi</h1>
                  <p>Je m'appelle Alioune Diagne, j'ai 21 ans et je suis en deuxieme anne de BTS SIO (Service Informatique aux Organisations)  l'IPSSI.
                    Je suis passionn par les technologies de l'information et de la communication, et j'ai choisi de suivre cette filire
                     car elle offre de nombreuses opportunits dans le domaine de l'informatique.
                     </p>
                    <div className='btn-contact'>
                      <a href="https://www.linkedin.com/in/alioune-diagne-281635253/" target="_blank" rel="noopener noreferrer"><button>LinkedIn<img src={linkedin} alt="" /></button></a>
                      <a href="https://github.com/DIAGNEUX" target="_blank" rel="noopener noreferrer"><button>Github<img src={gitHub} alt="" /></button></a>
                    </div>   
                </div>
              </div>
            </div>
            <div className='Skills'>
              <h1>Comptence acquises</h1>
              <div>
              <div class="grid_competence">
                    <div class="card" >
                      <div className='card-img'>
                      <img src={coding} alt="" />
                      </div>
                        <h1>Developpement Web</h1>
                        <h4>Conception et programmation d'un site web
                             en <span> HTML/CSS/JAVASCRIPT/PHP 
                             REACT/NODE JS </span>
                             </h4>
                    </div>
                    <div class="card" >
                      <div className='card-img'>
                      <img src={bdd} alt="" />
                      </div>
                        <h1>Gestion de BDD</h1>
                        <h4> Stocker, manipuler et grer les donnes
                             dans une base de donnes via MySQL ou MARIA BD.</h4>
                    </div>
                    <div class="card">
                      <div className='card-img'>
                      <img src={biggithub} alt="" />
                      </div>
                        <h1>Git et GitHub</h1>
                        <h4>Contrle de version, collaboration et gestion de projets avec Git et GitHub. </h4>
                    </div>
                </div>
              </div>
            </div>
            <div className="wrap_grid_them">
      <div className="grid_them">
        <div className="exper_prof">
          <h1 onClick={toggleExperience} className="btn_exper ">
            Experience professionnelle
            <img
              src={showExperience ? chevronUp : chevronDwon }
              alt=""
            />
          </h1>
          {showExperience && (
            <div className="experience">
       <ul>
        <h3> Projet Developpeur FullStack</h3>
        <p>Ipssi - Mars 2023  avril 2023</p>
        <li>cration d'une librairie</li>
        <li>l'utilisation des langages PHP MySQL JavaScript CSS HTML</li>
        <li>L'utilisation d'une API </li>
        <li>cration de logo sur Canvas</li>
    </ul>
    <ul>
        <h4> Projet Developpeur FrontEnd</h4>
        <p>Ipssi - Dcembre 2022  janvier 2023</p>
        <li>Cration de site vitrine</li>
        <li>L'utilisation des langages HTML CSS et JAVASCRIPT</li>
        <li>cration de charte graphique (logo) sur figma </li>
    </ul>
    <ul>
        <h4> Auto-Entrepreneur</h4>
        <p>Idrac - Mars 2022  mai 2022</p>
        <li>Cration d'une boutique en ligne(Wix)</li>
        <li>Vente de produit</li>
        <li>sondage et promotion sur le produit</li>
        <li>Etude de march</li>
    </ul>
            </div>
          )}
        </div>

        <div className="projet_prof">
          <h1 onClick={toggleProjet} className="btn_projet">
            Projet proffessionnel
            <img
             src={showProjet ? chevronUp : chevronDwon}
              alt=""
            />
          </h1>
          {showProjet && (
            <div className="projet">
              <p>Aprs l'obtention de mon BTS SIO option SLAM  l'IPSSI, je souhaite poursuivre mes tudes en
        faisant un Bachelor en dveloppement web et en applications. Ensuite, je prvois de consolider mes comptences en faisant un Master dans
        le mme domaine.
        Ces programmes me permettront d'acqurir des comptences spcifiques, telles que la programmation web,
        la conception d'interface utilisateur,
        la gestion de base de donnes, et bien plus encore.
        Mon objectif professionnel est de devenir un dveloppeur web et d'applications reconnu, en travaillant
        pour une entreprise innovante ou en crant ma propre entreprise. Mon domaine d'intrt est
        le dveloppement de solutions web pour les entreprises et les organisations. Je souhaite
        crer des applications web efficaces, faciles  utiliser et qui rpondent aux besoins des clients
    </p>
            </div>
          )}
        </div>
      </div>
    </div>
          </div>


      </div >  
      </div>
  );
};

export default Accueil;
